import React, { ReactNode } from "react";
import { UserCircleIcon, EditIcon, TrashBinIcon, EyeIcon, ReloadIcon, StopIcon, QrcodeIcon } from "@/icons";
import Button from "../Button";
import { PlayIcon } from "@heroicons/react/24/outline";

interface ButtonProps {
  // children: ReactNode; // Button text or content
  size?: 'xs' | 'sm' | 'md' | 'lg';
  variant?: 'success' | 'primary' | 'secondary' | 'danger';
  onClick?: () => void; // Click handler
  disabled?: boolean; // Disabled state
  className?: string; // Additional classes
  type?: "button" | "submit" | "reset"; // Button type
  title: string; // Tooltip text
  icon?: "edit" | "delete" | 'eye' | 'reload' | 'stop' | 'qrcode' | 'play'; // Icon type
}

const ButtonCard: React.FC<ButtonProps> = ({
  // children,
  size = "md",
  variant = "primary",
  onClick,
  className = "",
  disabled = false,
  type = "button",
  title = "Editar",
  icon = "edit" // Default icon is edit
}: ButtonProps & {
}) => {

  const geticon = (icon: string) => (
    <span className="flex items-center">
      {icon === "edit" ? (
        <EditIcon className="w-5 h-5 inline-block" />
      ) : icon === "eye" ? (
        <EyeIcon className="w-5 h-5 inline-block" />
      ) : icon === "reload" ? (
        <ReloadIcon className="w-5 h-5 inline-block" />
      ) : icon === "stop" ? (
        <StopIcon className="w-5 h-5 inline-block" />
      ) : icon === "qrcode" ? (
        <QrcodeIcon className="w-5 h-5 inline-block" />
      ) : icon === "play" ? (
        <PlayIcon className="w-5 h-5 inline-block text-green-500" />
      ) : (
        <TrashBinIcon className="w-5 h-5 inline-block" />
      )}
    </span>
  );
  const baseClasses = `${icon === 'edit' || icon === 'eye' || icon === 'reload' || icon === 'stop' || icon === 'qrcode' ? 'text-gray-400 hover:text-brand-500 dark:hover:text-brand-400' : 'text-red-500 dark:text-red-400'} rounded-md flex items-center justify-center transition-colors`;

  return (
    <Button
      variant={variant}
      size={size}
      onClick={() => onClick && onClick()}
      className={`${baseClasses} ${className}`}
      title={title}
      type={type}
      disabled={disabled}
    >
      {icon && geticon(icon)}
      {/* {children} */}
    </Button>
  );
};

export default ButtonCard;
